import React from 'react';

// Define TypeScript types for props
export type CertificateData = {
  studentName: string;
  courseTitle: string;
  completionDate: string;
  certificateNumber: string;
  templateImage: string;
  fontFamily: string;
  authorName?: string;
}

// This component is no longer used with pdf-lib approach
// Keeping it for backward compatibility but it won't be rendered
export const CertificateDocument: React.FC<CertificateData> = ({
  studentName,
  courseTitle,
  completionDate,
  certificateNumber,
  templateImage,
  fontFamily,
  authorName,
}) => {
  // This component is deprecated - PDF generation now uses pdf-lib directly
  return (
    <div>
      <h1>Certificate of Completion</h1>
      <p>Student: {studentName}</p>
      <p>Course: {courseTitle}</p>
      <p>Date: {completionDate}</p>
      <p>Certificate Number: {certificateNumber}</p>
      {authorName && <p>Author: {authorName}</p>}
    </div>
  );
};